import "./styles.css";
import { useState, useEffect } from "react";
const publicIp = require("public-ip");

export default function App() {
  const [myIp, setMyIp] = useState({});
  const getIp = async () => {
    let v4 = await publicIp.v4();
    // console.log(v4);
    //=> '46.5.21.123'
    let v6 = await publicIp.v6();
    // console.log(v6);
    //=> 'fe80::200:f8ff:fe21:67cf'
    setMyIp({ v4, v6 });
  };

  useEffect(() => {
    getIp();
  }, []);

  return (
    <div className="App">
      <h1>Ipv4: {myIp.v4}</h1>
      <h1>Ipv6: {myIp.v6}</h1>
      <button onClick={getIp}>Get IP</button>
    </div>
  );
}
